/************************************************************
	c01 - Base - WordPress Default
*************************************************************/
/**
 * WP alignments
 *
 */
.alignnone {
  margin: 5px auto 25px 0;
}

.aligncenter {
  float: none;
  display: block;
  margin: 0 auto 25px;
}

ul.aligncenter {
  margin-left: 18px;
}

.alignright,
.alignleft {
  display: inline;
  margin: 5px 0 25px;
}

.alignright {
  float: right;
  margin-left: 25px;
}

.alignleft {
  float: left;
  margin-right: 25px;
}

/**
 * WP Gutenberg Default Styles
 *
 */
@media only screen and (min-width: 768px) {
  /* Global Wide And Full Alignment Styles */
  .alignfull {
    margin-left: calc(50% - 50vw);
    margin-right: calc(50% - 50vw);
  }
  .alignwide {
    margin-left: calc(25% - 25vw);
    margin-right: calc(25% - 25vw);
  }
  .alignfull,
  .alignwide {
    width: auto;
    max-width: 1000%;
  }
  .page-sidebar-active .alignfull,
  .archive-sidebar-active .alignfull,
  .single-sidebar-active .alignfull, .page-sidebar-active
  .alignwide,
  .archive-sidebar-active
  .alignwide,
  .single-sidebar-active
  .alignwide {
    margin-left: 0;
    margin-right: 0;
    max-width: 100%;
  }
  .alignfull img,
  .alignwide img {
    display: block;
    margin: 0 auto;
  }
}

/* Pullquote */
.wp-block-pullquote {
  border: none;
  padding: 0;
  margin-bottom: 25px;
}

.wp-block-pullquote blockquote {
  border-left: 0;
  border-top: 8px solid #42ad61;
  border-bottom: 8px solid #42ad61;
  padding: 15px;
  text-align: center;
  margin: 0 auto;
}

.wp-block-pullquote blockquote * {
  max-width: 1110px;
  margin-left: auto;
  margin-right: auto;
}

.wp-block-pullquote.alignleft blockquote, .wp-block-pullquote.alignright blockquote {
  max-width: 100%;
}

/* Block Gallery */
.wp-block-embed,
.wp-block-image {
  margin-top: 25px;
  margin-bottom: 25px;
}

.wp-block-gallery:not(.alignwide):not(.alignfull) {
  margin-left: 0;
}

/* Block Latest Posts */
.wp-block-latest-posts {
  margin-left: 0;
  list-style: none;
}

.wp-block-latest-posts > li {
  margin-bottom: 15px;
}

.wp-block-latest-posts > li > a {
  padding: 20px;
  display: block;
  font-weight: 600;
}

.wp-block-latest-posts > li > a::before {
  content: "";
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  border-width: 0;
  border-style: solid;
  border-color: transparent;
}

.wp-block-latest-posts > li > a:hover::before, .wp-block-latest-posts > li > a:focus::before {
  border-width: 4px;
}

.wp-block-latest-posts.is-grid {
  margin-right: -16px;
}

.wp-block-latest-posts.has-dates > li > a {
  padding-top: 45px;
  padding-bottom: 17px;
}

.wp-block-latest-posts.has-dates > li > time {
  left: 20px;
  top: 17px;
}

/* Block Columns */
.wp-block-columns {
  margin-left: -15px;
  margin-right: -15px;
}

.wp-block-columns .wp-block-column {
  padding-left: 15px;
  padding-right: 15px;
}

/**
 * WP Caption
 *
 */
.wp-caption {
  max-width: 100%;
  padding: 10px 10px 0;
  text-align: center;
  margin-bottom: 25px;
  border-width: 1px;
  border-style: solid;
}

.wp-caption-text {
  padding: 10px;
  font-size: 90%;
}

.wp-caption img {
  border: 0 none;
  margin: 0;
  padding: 0;
  width: 100%;
}

/**
 * Page links for paginated posts
 *
 */
.page-links {
  display: block;
  width: 100%;
  clear: both;
  padding-top: 10px;
  padding-bottom: 10px;
}

.page-links span {
  display: inline-block;
  width: 30px;
  height: 30px;
  line-height: 30px;
  margin: 0 5px;
  text-align: center;
}

.page-links .page-links-title {
  width: auto;
  height: auto;
  margin-left: 0;
  margin-right: 15px;
}

/**
 * Text meant only for screen readers.
 *
 */
.screen-reader-text {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

/**
 * WP Gallery
 *
 */
.gallery {
  margin-bottom: 17px;
  margin-left: -8px;
  margin-right: -8px;
}

.gallery-caption {
  word-wrap: break-word;
}

.gallery-item {
  display: inline-block;
  text-align: left;
  vertical-align: top;
  margin: 0 0 1em;
  padding: 0 0.5em 0 0.5em;
  width: 100%;
}

.gallery-columns-1 .gallery-item {
  max-width: 100%;
}

.gallery-columns-2 .gallery-item {
  max-width: 50%;
}

.gallery-columns-3 .gallery-item {
  max-width: 33.3333333333%;
}

.gallery-columns-4 .gallery-item {
  max-width: 25%;
}

.gallery-columns-5 .gallery-item {
  max-width: 20%;
}

.gallery-columns-6 .gallery-item {
  max-width: 16.6666666667%;
}

.gallery-columns-7 .gallery-item {
  max-width: 14.2857142857%;
}

.gallery-columns-8 .gallery-item {
  max-width: 12.5%;
}

.gallery-columns-9 .gallery-item {
  max-width: 11.1111111111%;
}

.gallery-columns-10 .gallery-item {
  max-width: 10%;
}

.gallery-columns-11 .gallery-item {
  max-width: 9.0909090909%;
}

.gallery-columns-12 .gallery-item {
  max-width: 8.3333333333%;
}

.gallery-item a,
.gallery-item a:hover,
.gallery-item a:focus {
  display: inline;
  width: 100%;
}

.gallery-item img {
  display: block;
  width: 100%;
}

.gallery-item img:hover, .gallery-item img:focus {
  transform: scale(1.1);
}

/**
 * Sticky Post
 *
 */
.sticky {
  /* sticky css */
}

/**
 * Post Password Form
 *
 */
.post-password-form label {
  float: left;
  margin-right: 15px;
}

.post-password-form input[type="password"] {
  margin-left: 20px;
  margin-bottom: 20px;
  padding: 8px 12px;
