}

/**
 * WP Comment Form
 *
 */
#commentform fieldset {
  clear: both;
}

#commentform textarea {
  height: 150px;
  padding: 10px;
  width: 100%;
  display: block;
}

#commentform input[type="text"], #commentform input[type="email"], #commentform input[type="url"] {
